html{
	font-style: 62.5%; color:$white;
}
html, body {height: 100%}
body {padding:0; color:$white; font-size:1.4rem; line-height:2.2rem;}
.nmb{
	margin-bottom: 0 !important
}
a {
	text-decoration:none;
	@include transition(.2s); 
	text-decoration:underline;
	&:hover {text-decoration:none}
}
p, li {
	font-size:1.4rem; 
	line-height:2.2rem; 
	margin:0 0 3.5rem}
p a, li a {text-decoration:underline; color:$white}
p a:hover, li a:hover {text-decoration:none}
ul {margin-bottom:1.5rem}
li {margin:0}
h1,h2,h3,h4,h5,h6 {font-weight:400; font-size:3.0rem;  margin:0 0 2.0rem; line-height:3.5rem}
figure {padding:0; margin:0}
address {font-style:normal}
h1,h2,h3,h4,h5,h6{
	font-weight: $b;
	line-height:1.3em;
}
.underline{
	display: inline;
	padding-bottom:.05em;
	border-bottom:2px solid $white;
	&--green{
		border-color:$green;
	}
	&--orange{
		border-color:$orange;
	}
}
@media only screen and (min-width: 768px){
	.mobile{
		display: none !important;
	}
}
@media only screen and (max-width: 767px){
	.desktop{
		display: none !important;
	}
}
h1{
	font-size: 3.6rem;
	+ p {
		margin-top: 1.5em;
	}
	@media only screen and (max-width: 767px){
		font-size: 3rem;
	} 
}
h2{
	font-size: 3rem;
	@media only screen and (max-width: 767px){
		font-size: 2.4rem;
	} 
	
}
h3{
	font-size: 2.4rem;
	@media only screen and (max-width: 767px){
		font-size: 2rem;
	} 
}
h4{
	.underline{
		border-bottom-width:1px;
	}
	font-size: 2rem;
	@media only screen and (max-width: 767px){
		font-size: 1.6rem;
	} 
}
h5{
	font-size: 1.6rem;
	line-height: 2.5rem;
	font-weight: 300;
	@media only screen and (max-width: 767px){
		font-size: 1.6rem;
		line-height: 2.5rem;
	}	
}
h6{
	font-size: 1.8rem;
	line-height: 2.5rem;
	// font-weight: 300;
	color:$white;
	@media only screen and (max-width: 767px){
		font-size: 1.8rem;
		line-height: 2.5rem;
	}
}
li, p{
	font-size: 1.8rem;
	line-height:2em;
	font-weight: $n;
	+ h4{
		margin-top: 1.35em;
	}
	a{
		text-decoration: none;
		color:inherit;
		&:hover{
			color:$orange;
		}
	}
	&:last-child{
		margin-bottom: 0;
	}
	@media only screen and (max-width: 767px){
		font-size: 1.4rem;
	}
	&.x-large{
		font-size: 2.8rem;
		@media only screen and (max-width: 767px){
			font-size: 1.8rem;
		}
	}
	.larger,
	.large{
		font-size: 1.8rem;
		@media only screen and (max-width: 767px){
			font-size: 1.6rem;
		}
	}
}
p{
	@media only screen and (max-width: 767px){
		margin-bottom: 2.0rem;
	}
}
li{}
ul, ol{}
ul{}
ol{}
blockquote{}
figure{
	position: relative;;
	font:0;
	display: block;
	width: 100%;
	@include thumbnail;
	
}
figcaption{}