.logo{
	max-width: 344px;
	width: 100%;
	display: block;
	margin:0 auto 20px 0;
}
.landing{
	height: 100vh;
	background-image:url(../img/dots.png);
	box-sizing: border-box;
	position:relative;
	// overflow: hidden;
	.table{
		position:relative;
		.cell{
			position: relative;
			z-index: 2
		}
		.container{
			padding:150px 0;
		@media only screen and (max-width: 767px){
			padding:0;
		}
		}
		@media only screen and (max-width: 767px){
			&::after{
				content:'';
				display: block;
				position:absolute;
				@include icon;
				z-index: 1;
				top:50%;
				right: 0;
				transform:translateY(-50%);
				background-position:center right;
				background-image:url(../img/right-motif.png);
				width: 90%;
				height: 90%;
				max-width: 325px;
				max-height: 560px;
			}
		}
	}
	@media only screen and (max-width: 767px){
		height: auto;
		padding:150px 0 50px 0;
	}
	img{
		&:not(.logo){
			max-width: 100%;
		}
		&.logo{
			width: 70%;
			@media only screen and (max-width: 767px){
				width: 50%;
			}
		}
	}
	&::before{
		content:'';
		display: block;
		position:absolute;
		top: 0;
		left: 0;
		right: 0;
		height: 15px;
		z-index: 10;
		/* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#00f072+0,0cdbdb+100 */
		background: rgb(0,240,114); /* Old browsers */
		background: -moz-linear-gradient(left, rgba(0,240,114,1) 0%, rgba(12,219,219,1) 100%); /* FF3.6-15 */
		background: -webkit-linear-gradient(left, rgba(0,240,114,1) 0%,rgba(12,219,219,1) 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to right, rgba(0,240,114,1) 0%,rgba(12,219,219,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00f072', endColorstr='#0cdbdb',GradientType=1 ); /* IE6-9 */
	}
	.f-left{
		width: calc(100% - 410px);
		position:relative;
		z-index: 1;
		@media only screen and (max-width: 767px){
			position:static;
		}
		&::before{
			content:'';
			display: block;
			position:fixed;
			@include icon;
			z-index: 2;
			bottom:0;
			left: 0;
			background-position:bottom left;
			background-image:url(../img/left-motif.png);
			width: 90%;
			height: 90%;
			max-width: 370px;
			max-height: 235px;
			@media only screen and (max-width: 767px){
				position:absolute;
				bottom:-50px;
			}
		}
		p{
			max-width: 500px;
		@media only screen and (max-width: 767px){
			max-width: 100%;
		}
		}
		@media only screen and (max-width: 767px){
		width: 100%;
		float: none;
			&:not(:last-child){
				margin-bottom: 50px;
			}
		}
	}
	.f-right{
		max-width: 380px;
		position:relative;
		z-index: 2;
		@media only screen and (max-width: 767px){
			float: none;
			width: 100%;
			max-width: 100%;
			img{
				max-width: 400px;
				width: 100%;
				margin:0 auto;
				float: none;
				display: block;
			}
		}
	}
}
.contact-wrap{
	position:relative;
	padding:25px 0;
	p{
		font-size: 2rem;
		line-height: 1.35;
	}
	*{
		position: relative;
		z-index: 2;
		&:last-child{
			margin-bottom: 0;
		}
	}
	&::before{
		content:'';
		width: 400vw;
		left: 50%;
		position:absolute;
		z-index: 1;
		top: 0;
		bottom:0;
		left: 50%;
		transform:translateX(-50%);
		background-color:#1A2B3C;
	}
	
	&::after{
		content:'';
		display: block;
		position:fixed;
		@include icon;
		z-index: 1;
		top:50%;
		right: 0;
		transform:translateY(-50%);
		background-position:center right;
		background-image:url(../img/right-motif.png);
		width: 90%;
		height: 90%;
		max-width: 325px;
		max-height: 560px;
			@media only screen and (max-width: 767px){
				display: none;
			}
	}
}
@media only screen and (max-width: 767px){
	br{
		display: none;
	}
}
.pullout{
	position: absolute;
	top: 130px;
	right: 0;
	display: block;
	text-align: right;
	text-decoration: none;
	width: 225px;
	height: 85px;
	z-index: 3;
	@media only screen and (max-width: 767px){
		width: 100%;
		top: 50px;
	}
	@media only screen and (max-width: 1380px){
		top: 40px;
	}
	p{
		color:$white;
		width: 160px;
		display: block;
		position:absolute;
		top: 50%;
		transform:translateY(-50%);
		// left: 100%;
		@include transition(.2s);
		z-index: 1;
		left: 0;
		font-size: 1.6rem;
		line-height: 1.5;
		@media only screen and (max-width: 767px){
			right: 50px;
			left: auto;
			width: 150px;
		}
	}
	.icon{
		position:absolute;
		top: 50%;
		right: 0;
		z-index: 2;
		
		background-size:contain;
		background-position: right center;
		background-repeat:no-repeat;
		transform:translateY(-50%);
		width: 42px;
		height: 83px;
		background-image:url(../img/pullout-retina.png);
	}
	&:hover p{
		// color:$orange;
	}
}